.post {
  width: 100%;
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  margin-bottom: 30px;

  .postWrapper {
    padding: 10px;

    .postTop {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .postTopRight {
        cursor: pointer;
        .deletePost,
        .editPost {
          position: absolute;
          right: 20px;
          top: 0px;
          width: 100px;
          height: 30px;
          background-color: rgba(128, 128, 128, 0.303);
          border-radius: 5px;
          z-index: 1;
          color: rgb(65, 65, 65);
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          &:hover {
            background-color: rgba(128, 128, 128, 0.102);
          }
        }
        .editPost {
          margin-top: 50px;
        }
      }
      .postTopLeft {
        display: flex;
        align-items: center;

        .postProfileImg {
          width: 32px;
          height: 32px;
          border-radius: 50%;
          object-fit: cover;
        }

        .postUsername {
          font-size: 15px;
          font-weight: 500;
          margin: 0 10px;
        }

        .postDate {
          font-size: 12px;
        }
      }
    }

    .postCenter {
      margin: 20px 0;

      .postImg {
        margin-top: 20px;
        width: 100%;
        max-height: 300px;
        min-width: 0;
        object-fit: contain;
      }
    }

    .postBottom {
      display: flex;
      flex-direction: column;
      gap: 10px;

      .postBottomTop {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .postBottomLeft {
          display: flex;
          align-items: center;

          .likeIcon {
            width: 24px;
            height: 24px;
            margin-right: 5px;
            cursor: pointer;
          }

          .postLikeCounter {
            font-size: 15px;
          }
        }

        .postBottomRight {
          .postCommentText {
            cursor: pointer;
            border-bottom: 1px dashed gray;
            font-size: 15px;
          }
        }
      }

      hr {
        border: 1px solid rgb(224, 224, 224);
      }

      .commentInput {
        display: flex;
        justify-content: space-between;
        gap: 10px;
        .icon {
          font-size: 13px;
          transform: translateY(10%);
        }
        input {
          background-color: rgb(243, 243, 243);
          border: 0;
          border-radius: 40px;
          padding: 10px;
          flex: 9;
          height: 8px;

          &:focus {
            outline: 0;
          }
        }

        button {
          flex: 1;
          border: none;
          padding: 7px;
          border-radius: 5px;
          background-color: rgb(27, 138, 212);
          font-weight: 500;
          cursor: pointer;
          color: white;
        }
      }
    }
  }
}
