.topbarContainer{
    grid-column: 1 / span 3;
    grid-row: 1;
    height: 50px;
    width: 100%;
    background-color: #1877f2;
    display: flex;
    gap: 30px;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 10;
    @media (max-width: 800px){
        .topbarCenter{
            display: none;
        }
    }
}

.logo{
    font-size: 24px;
    margin-left: 20px;
    font-weight: bold;
    color: white;
    cursor: pointer;
}

.searchbar{
    width: 100%;
    height: 30px;
    background-color: white;
    border-radius: 30px;
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.searchIcon{
    font-size: 20px !important;
    margin-left: 10px;
}

.searchInput{
    border: none;
    width: 70%;
}

.searchInput:focus{
    outline: none;
}

.topbarLeft{
    flex:1.7;
    
}
.topbarCenter{
    flex:5;

}
.topbarRight{
    flex:4;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    // padding-right: 30px;
    color: white;
}

.topbarLink{
    margin-right: 10px;
    font-size: 14px;
    cursor: pointer;
}

.topbarIcons{
    display: flex;
    &>*{
        margin-right: 40px;
    }
}


.topbarIconItem{
    margin-right: 15px;
    cursor: pointer;
    position: relative;
}

.topbarIconBadge{
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
    color: white;
    position: absolute;
    top: -5px;
    right: -5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
}

.topbarImg{
    width: 32px;
    margin-left: 1.5vw;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
    border: 1.5px solid white;
    margin-right: 20px;
}

.hamburgerBtn{
    display: none;
    border: 0;
    background-color: transparent;
    color: white;
    position: absolute;
    top: 10;
    left: 20px;
    padding: 0;
}
@media(max-width:1100px){
    .hamburgerBtn{
        display: block;
    }
    .logo{
        display: none;
    }
}
@media(max-width:800px){
    
    .logo{
        display: block;
        margin-left: 50px;
    }
}