.post-edit {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(128, 128, 128, 0.652);
  border-radius: 10px;
  width: 100%;
  height: 100%;
  background-color: rgba(128, 128, 128, 0.541);
  .share {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: max-content;
    margin: auto;
    background-color: rgba(255, 255, 255, 0.724);
    .wrapper {
      .shareImgContainer {
        width: 30vw;
        margin: auto;
      }
      .shareTop {
        .shareInput {
          background-color: transparent;
        }
      }
    }
  }

  z-index: 5;
}
