::-webkit-scrollbar{
    width: 5px;
    display: none;
}
::-webkit-scrollbar-track{
    background-color: #f1f1f1;
    
}
::-webkit-scrollbar-thumb{
    background-color: rgb(206, 206, 206);
}

.sidebar{
    background-color: white;
    z-index: 5;
    // flex: 3;
    grid-column: 1;
    grid-row: 2;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    position: sticky;
    top: 50px;
    &:hover::-webkit-scrollbar{
            display: block;
    }
    .wrapper{
        padding: 20px;  
        .list{
            padding: 0;
            .listItem{
                display: flex;
                align-items: center;
                margin-bottom: 20px;
                text-decoration: 0;
                color: rgb(60, 60, 60);
                .icon{
                    margin-right: 15px;
                }
            };
        }      

        .sidebarButton{
            width: 150px;
            border: none;
            padding: 10px;
            border-radius: 5px;
            font-weight: 500;
        }

        .sidebarHr{
            margin:20px 0 ;
        }

        .friendList{
            padding: 0;
            margin: 0;
            list-style: none;
           
        }

        
    }
    @media (max-width: 1100px){
        // display: none;
        transform: translateX(-100%);
        transition: .6s;
        width: max-content;
        &.shown{
            width: max-content;
            background-color: white;
            // margin-top: 50px;
            transform: translateX(0%);
            transition: .6s;
            z-index: 20;
        }
    }
}