.rightbar {
  grid-column: 3;
  grid-row: 2;
  padding: 10px;
  .rightbarWrapper {
    padding: 20px 20px 0 0;

    .birthdayContainer {
      display: flex;
      align-items: center;

      .birthdayImg {
        width: 40px;
        height: 40px;
        margin-right: 10px;
      }

      .birthdayText {
        font-weight: 300;
        font-size: 15px;
      }
    }

    .rightbarAd {
      width: 100%;
      border-radius: 10px;
      margin: 30px 0;
    }

    .rightbarTitle {
      margin-bottom: 20px;
    }

    .rightbarFriendList {
      padding: 0;
      margin: 0;
      list-style: none;
    }

    .rightbarTitle {
      font-size: 18px;
      font-weight: 500;
      margin-bottom: 10px;
    }

    .rightbarInfo {
      margin-bottom: 30px;
      .rightbarInfoItem {
        margin-bottom: 10px;

        .rightbarInfoKey {
          font-weight: 500;
          margin-right: 15px;
          color: #555;
        }

        .rightbarInfoValue {
          font-weight: 300;
        }
      }
    }

    .rightbarFollowings {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
      gap: 10px;

      .rightbarFollowing {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
        cursor: pointer;

        .rightbarFollowingImg {
          width: 100px;
          height: 100px;
          object-fit: cover;
          border-radius: 5px;
        }
      }
    }

    .rightbarFollowBtn {
      margin-bottom: 10px;
      border: 0;
      background-color: #1872f2;
      color: white;
      border-radius: 5px;
      padding: 5px 10px;
      display: flex;
      align-items: center;
      cursor: pointer;
      font-size: 16px;
      font-weight: 500;
      &:hover {
        background-color: #5b9cf6;
      }
      &:focus {
        outline: 0;
      }
    }
  }
  @media (max-width: 800px) {
    display: none;
  }
}
