.commentsContainer{
    display: flex;
    flex-direction: column;
    .comment{
        display: flex;
        gap: 10px;
        font-size: 14px;
        margin-bottom: 15px;
        .commentImgContainer{
            .commentImg{
                width: 40px;
                height: 40px;
                border-radius: 50%;
                object-fit: cover;
            }
        }
        .commentsDetails{
            padding: 10px;
            border-radius: 15px;
            gap:5px;
            background-color: rgb(240, 240, 240);
            display: flex;
            flex-direction: column;
            .commentUsername{
                font-weight: 500;

            }
            .time{
                font-size: 11px;
                color: gray;
            }
        }
    }
}