.share {
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  margin-bottom: 30px;

  .wrapper {
    padding: 10px;
    .shareTop {
      display: flex;
      align-items: center;
      .input {
        position: absolute;
        left: 0;
        visibility: hidden;
      }
      .shareInput {
        border: none;
        width: 80%;
        &:focus {
          outline: none;
        }
      }
      .profileImg {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        object-fit: cover;
        margin-right: 10px;
      }
    }

    hr {
      margin: 20px;
    }
    .shareBottom {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .shareOptions {
        display: flex;
        margin-left: 20px;
        .emojiOption {
          position: relative;
          .emojiBoard {
            display: grid;
            grid-template-columns: repeat(5, auto);
            z-index: 5;
            padding: 5px;
            position: absolute;
            top: 35px;
            background-color: rgb(238, 238, 238);
            width: 200px;
            height: 100px;
            overflow-y: scroll;
            border-radius: 10px;
            .emoji {
              align-self: center;
              justify-self: center;
              padding: 2px;
              &:hover {
                background-color: rgb(208, 208, 208);
                border-radius: 50%;
              }
            }
          }
        }
        .option {
          display: flex;
          align-items: center;
          margin-right: 15px;
          cursor: pointer;
          .shareIcon {
            font-size: 18px;
            margin-right: 3px;
          }
          .optionText {
            font-size: 14px;
            font-weight: 500;
          }
        }
      }
    }
    .shareImgContainer {
      padding: 0 20px 10px 20px;
      position: relative;
      .shareImg {
        width: 100%;
        object-fit: cover;
      }
      .cancelBtn {
        z-index: 2;
        position: absolute;
        top: 0;
        right: 20;
        .shareCancelImg {
          width: 3vw;
          fill: white;
          padding: 10px;
          cursor: pointer;
          &:hover {
            opacity: 0.7;
          }
        }
      }
    }
  }

  .shareButton {
    border: none;
    padding: 7px;
    border-radius: 5px;
    background-color: green;
    font-weight: 500;
    margin-right: 20px;
    cursor: pointer;
    color: white;
  }
}

@media (max-width: 800px) {
  .options:nth-child(2) {
    display: none;
  }
}
