.register{
    width: 100vw;
    height: 100vh;
    background-color: #f0f2f5;
    display: flex;
    align-items: center;
    justify-content: center;
    .registerWrapper{
        height: 70%;
        width: 70%;
        display: flex;
        gap: 20px;
        .registerLeft, .registerRight{
            flex:1;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
        
        .registerLeft{
            .registerLogo{
                font-size: 50px;
                font-weight: 800;
                color: #1775ee;
                margin-bottom: 10px;
            }
            .registerDesc{
                font-style: 24px;
            }
        }

        .registerRight{
            .registerBox{
                height: 400px;
                padding: 20px;
                background-color: white;
                border-radius: 10px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                .registerInput{
                    height: 50px;
                    border-radius: 10px;
                    border: 1px solid gray;
                    font-size: 18px;
                    padding-left: 20px;
                    &:focus{
                        outline: 0;
                    }
                }

                .registerBtn{
                    height: 50px;
                    border-radius: 10px;
                    border: 0;
                    background-color: #1775ee;
                    color: white;
                    font-size: 20px;
                    font-weight: 500;
                }

                .registerForgot{
                    text-align: center;
                    color: #1775ee;
                }

                .registerRegisterBtn{
                    height: 50px;
                    align-self: center;
                    width: 60%;
                    border-radius: 10px;
                    border: 0;
                    background-color: #79cd5d;
                    color: white;
                    font-size: 20px;
                    font-weight: 500;
                }
            }
        }
    }
}

