.profile {
  display: grid;
  grid-template-columns: 2fr 9fr;

  .profileTopbar {
  }

  .profileSidebar {
  }

  .profileRight {
    display: grid;
    grid-template-columns: 3.5fr 1.2fr;

    .profileRightTop {
      grid-column: 1 / span 2;
      grid-row: 1;

      .profileCover {
        height: 320px;
        position: relative;

        .profileLabel {
          .profileImgContainer {
            position: relative;

            &.changeable:hover {
              cursor: pointer;

              .profileUserImg {
                filter: brightness(50%);
              }

              .changeProfileImgText {
                display: block;
                z-index: 200;
              }
            }

            .changeProfileImgText {
              display: none;
              position: absolute;
              color: white;
              top: -30px;
              margin-left: auto;
              margin-right: auto;
              left: 0;
              right: 0;
              text-align: center;
            }

            .profileUserImg {
              width: 150px;
              height: 150px;
              border-radius: 50%;
              object-fit: cover;
              position: absolute;
              top: -100px;
              margin-left: auto;
              margin-right: auto;
              left: 0;
              right: 0;
              text-align: center;
              border: 3px solid white;
            }
          }
        }
        .coverLabel {
          .coverImgContainer {
            position: relative;

            &.changeable:hover {
              cursor: pointer;

              .profileCoverImg {
                filter: brightness(50%);
              }

              .changeCoverImgText {
                display: block;
                z-index: 200;
              }
            }

            .changeCoverImgText {
              display: none;
              position: absolute;
              color: white;
              top: 30%;
              font-size: 30px;
              margin-left: auto;
              margin-right: auto;
              left: 0;
              right: 0;
              text-align: center;
            }

            .profileCoverImg {
              width: 100%;
              height: 250px;
              object-fit: cover;
            }
          }
        }
      }

      .profileInfo {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        .profileInfoName {
          font-size: 24px;
        }

        .profileInfoDesc {
          font-weight: 300;
        }
      }
    }

    .rightbar {
      grid-column: 2;
      grid-row: 2;
    }

    .feed {
      grid-column: 1;
      grid-row: 2;
    }
  }

  .profileRightBottom {
    display: flex;
  }
}
@media (max-width: 1100px) {
  .profileRight {
    grid-column: 1/-1;
    grid-row: 2;
  }
}

@media (max-width: 800px) {
  .profile {
    .profileRight {
      .feed {
        grid-column: 1/-1;
        grid-row: 2;
      }

      .rightbarWrapper {
        display: none !important;
      }
    }
  }
}
