.notifications{
    width: 300px;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 39px;
    right: 0px;
    padding: 10px;
    background-color: white;
    .notificationsTop{
        padding-bottom: 5px;
        background-color: white;
        display: flex;
        justify-content: space-between;
        .notificationTitle{
            font-weight: 600;
            color: rgb(54, 54, 54);
        }
        .notificationMarkBtn{
            color: rgb(113, 171, 237);
        }
    }
    .notificationContainer{
        background-color: white;
        border-top: 1px solid rgb(233, 230, 230);
        display: flex;
        flex-direction: column;
        .notificationItem{
            display: flex;
            align-items: center;
            .notificationItemImg{
                padding: 10px;
                width: 30px;
                height: 30px;
                object-fit: cover;
                border-radius: 50%;
            }
            .notificationItemDetails{
                color: black;
                display: flex;
                flex-direction: column;
            }
        }
    }
}