.conversation{
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    margin-top: 20px;
    &:hover{
        background-color: rgb(237, 237, 237);
    }
    .conversationImg{
        width: 40px;
        height: 40px;
        border-radius: 50%;
        object-fit: cover;
        margin-right: 20px;
    }
    .conversationName{
       font-weight: 500; 
    }
    &.marked{
        background-color: rgb(234, 234, 234);
    }
}