.feed{
    // flex:5.5;
    grid-column: 2;
    grid-row: 2;
    margin: 20px;
 .wrapper{
       //
       display: grid;
       grid-auto-flow: row;
      .noPosts{
            text-align: center;
            padding: 20px;
            background-color: rgb(246, 246, 246);
            height: 10vh;
            border-radius: 20px;
      }       
 }
 
}


@media (max-width:1100px){
    
      .homeContainer .feed{
          grid-column: 1/span 2;
      }
  }
@media (max-width:800px){
    
      .homeContainer .feed{
          grid-column: 1/ -1;
      }
  }