.messenger {
  height: calc(100vh - 70px);
  display: flex;

  @media (max-width: 800px) {
    .chatOnline {
      display: none;
    }
  }

  .chatMenuWrapper,
  .chatBoxWrapper,
  .chatOnlineWrapper {
    padding: 10px;
    height: 100%;
  }

  .chatMenu {
    flex: 1.5;
    font-size: 12px;
    .chatMenuWrapper {
      background-color: white;
      .chatMenuInput {
        width: 90%;
        padding: 10px 0;
        border: 0;
        border-bottom: 1px solid gray;
      }
      overflow-y: scroll;
    }

    &.shown {
      transform: translateX(0);
      transition: 0.6s;
    }

    @media (max-width: 800px) {
      position: absolute;
      transform: translateX(-100%);
      transition: 0.6s;
    }
  }

  .chatBox {
    flex: 5.5;

    .chatBoxWrapper {
      display: flex;
      flex-direction: column;
      overflow-y: hidden;

      &:hover ::-webkit-scrollbar {
        display: block;
      }

      .chatBoxTop {
        height: 100%;
        overflow-y: scroll;
        padding-right: 10px;
      }

      .chatBoxBottom {
        margin-top: 5px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
        gap: 10px;

        .chatMessageInput {
          width: 85%;
          height: 90px;
          padding: 10px;
          border: 0;
          background-color: rgb(244, 244, 244);
          border-radius: 10px;
        }

        .chatSubmitButton {
          background-color: #1877f2;
          width: 70px;
          height: 40px;
          border: 0;
          border-radius: 5px;
          color: white;
        }
      }
    }
  }

  .chatOnline {
    flex: 1.5;
    h2 {
      color: rgb(120, 120, 120);
    }

    .chatOnlineWrapper {
    }
  }

  .menuBtn {
    border: 0;
    background-color: transparent;
    color: white;
    position: absolute;
    top: 11px;
    left: 20px;
    padding: 0;
    display: none;
    z-index: 10;

    @media (max-width: 800px) {
      display: block;
      position: absolute;
    }
  }
}

::-webkit-scrollbar {
  width: 5px;
  display: block;
}

::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(206, 206, 206);
}
